/* header */
.site-header {
	position: relative;
	padding-top: 6px;
	padding-bottom: 30px;
	background: rgba(255, 255, 255, 0.6705882353);
	box-shadow: 0px 4px 24px 0px rgba(0, 0, 0, 0.1215686275);
	z-index: 100;
}
.site-header::before {
	content: "";
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	pointer-events: none;
	box-shadow: 0px 12px 24px 0px rgba(0, 0, 0, 0.1215686275) inset;
}
.site-header .content-holder {
	position: relative;
	display: flex;
	align-items: center;
	justify-content: center;
	gap: 86px;
	z-index: 1;
}
@media (min-width: 575px) {
	.site-header .content-holder {
		padding-top: 20px;
	}
}
@media (max-width: 767px) {
	.site-header .content-holder {
		gap: 40px;
	}
}
@media (max-width: 574px) {
	.site-header .content-holder {
		flex-direction: column;
	}
}
.site-header .content-holder .logo {
	display: inline-flex;
	width: 100%;
	max-width: 138px;
}
.site-header .content-holder .logo img {
	width: 100%;
	height: auto;
}
.site-header .content-holder .title-holder {
	display: flex;
	align-items: center;
	gap: 16px;
	margin-bottom: 16px;
}
@media (max-width: 574px) {
	.site-header .content-holder .title-holder {
		flex-direction: column;
	}
}
.site-header .content-holder .title-holder .header-title {
	margin-bottom: 0;
	font-family: "Montserrat", sans-serif;
	font-weight: 500;
	font-size: 24px;
}
.site-header .content-holder ul {
	margin: 0 0 19px 0;
	padding: 0;
	list-style: none;
	display: flex;
	flex-wrap: wrap;
	gap: 17px;
}
@media (max-width: 574px) {
	.site-header .content-holder ul {
		justify-content: center;
		gap: 12px;
	}
}
.site-header .content-holder ul li:first-child {
	margin-right: 11px;
}
.site-header .content-holder ul li p {
	margin-bottom: 0;
}
.site-header .content-holder .datepickers {
	display: flex;
	align-items: center;
	font-size: 14px;
}
@media (max-width: 574px) {
	.site-header .content-holder .datepickers {
		flex-direction: column;
		gap: 20px;
	}
}
.site-header .content-holder .datepickers p {
	margin-bottom: 0;
}
@media (min-width: 575px) {
	.site-header .content-holder .datepickers p {
		margin-right: 11px;
	}
}
