		max-width: 162px;
	}
}
@media (max-width: 574px) {
	.site-header .content-holder .datepickers .input-datepicker {
		width: 100%;
	}
}
@media (min-width: 575px) {
	.site-header .content-holder .datepickers .input-datepicker:last-child {
		margin-left: 22px;
	}
}
.site-header .open-menu {
	position: absolute;
	top: 50%;
	transform: translateY(-50%);
	right: 2rem;
	width: 2.4rem;
	height: 2.4rem;
	display: block;
	font-size: 0;
	line-height: 0;
	z-index: 100;
}
@media (min-width: 992px) {
	.site-header .open-menu {
		display: none;
	}
}
@media (max-width: 767px) {
	.site-header .open-menu {
		right: 2.4rem;
	}
}
.site-header .open-menu span {
	position: absolute;
	top: 50%;
	left: 0;
	width: 100%;
	background: white;
	border-radius: 0;
	height: 0.2rem;
	transition: all 0.3s ease;
}
.site-header .open-menu span:nth-child(1) {
	margin-top: -1rem;
}
.site-header .open-menu span:nth-child(2) {
	margin-top: -0.1rem;
}
.site-header .open-menu span:nth-child(3) {
	margin-top: 0.8rem;
}
.menu-opened .site-header .open-menu span:nth-child(1) {
	margin-top: 0;
	transform: rotate(45deg);
}
.menu-opened .site-header .open-menu span:nth-child(2) {
	opacity: 0;
}
.menu-opened .site-header .open-menu span:nth-child(3) {
	margin-top: 0;
	transform: rotate(-45deg);
}

